.exhibition > .shop {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 140px;
}
.exhibition > .shop > ul {
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    overflow-x: scroll;
}
.exhibition > .shop > ul > li {
    padding-top: 20px;
    height: 100%;
    width: 120px;
    text-align: center;
    margin: 0 8px;
    background: #f1f1f1;
    flex: 0 0 auto;
    box-sizing: border-box;
}
.exhibition > .shop > ul > li > .img {
    height: 48px;
}
.exhibition > .shop > ul > li > .money {
    height: 20px;
    line-height: 20px;
    font-size: 12px;
}

.exhibition > .shop > ul > li > .btn {
    
    margin-top: 5px;
    height: 26px;
    text-align: left;
}
.exhibition > .shop > ul > li > .btn > .but {
    height: 26px;
    line-height: 26px;
    font-size: 14px;
    border: none;
    border-radius: 8px;
    float: left;
    margin-left: 10px;
}
.exhibition > .shop > ul > li > .btn > div {
    height: 26px;
    line-height: 26px;
    font-size: 12px;
    float: left;
    margin-left: 10px;
}
.exhibition > .shop > ul > li > .btn > div > .jia {
    padding: 2px 5px;
    cursor: pointer;
    background: #fff;
}
.exhibition > .shop > ul > li > .btn > div > .jian {
    padding: 2px 5px;
    cursor: pointer;
    background: #fff;
}