.house {
    width: 100%;
    height: 100%;
}

.house > ul{
    width: 100%;
    height: 100%;
    list-style: none;
    padding: 0;
    margin: 0;
    
    overflow-x: scroll;
    display: flex;
}

.house > ul > li{
    flex: 0 0 auto;
    width: 150px;
    background: #f1f1f1;
    text-align: center;
    margin: 0 8px;
}

.house > ul > li > .img{
    margin-top: 5px;
    height: 48px;
}
.house > ul > li > .num{
    font-size: 14px;
    line-height: 30px;
    margin-top: 10px;
}
.house > ul > li > .num{
    font-size: 14px;
    line-height: 30px;
}

.house > ul > li > .btn{
    font-size: 14px;
    line-height: 30px;
}

.house > ul > li > .btn > button{
    font-size: 14px;
    line-height: 30px;
    background: #666;
    border-radius: 8px;
    border: none;
    padding: 0 8px;
    cursor: pointer;
}
.house > ul > li > .btn > span{
    margin-left: 10px;
}