
.seed {
    width: 100%;
    height: 100%;
}

.seed > ul{
    width: 100%;
    height: 100%;
    list-style: none;
    padding: 0;
    margin: 0;
    
    overflow-x: scroll;
    display: flex;
}

.seed > ul > li{
    flex: 0 0 auto;
    width: 100px;
    background: #f1f1f1;
    text-align: center;
    padding-top: 10px;
    margin: 0 8px;
}

.seed > ul > li > .img{
    margin-top: 20px;
    height: 48px;
}
.seed > ul > li > .num{
    font-size: 14px;
    line-height: 30px;
    margin-top: 10px;
}